

.skills {
        background-color: white;
        /* min-height: 100%; */
}


.skills__list-item {
  display: inline-block;
  margin: 1%;
  padding: 5%;
  border-radius: 4px;
  color: white;
 -webkit-animation: borders 1s normal forwards;
  animation: borders 1s normal forwards;
}

.heading--margin {
    margin: 5% 0;
}


/*cubes*/

.skills__list {
    height: auto;
    width: 65%;
    list-style-type: none;
    margin: 0 auto;
    padding: 0;
    border-radius: 4px;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: row;
        flex-direction: row;
    -ms-flex-pack: distribute;
        justify-content: space-around;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
}

@-webkit-keyframes borders {
   from {
     /* transform: scale(1, 1); */

     background-color: white;
   }

   to {
     /* transform: scale(1.1, 1.1); */
     border-radius: 5px;
   }
}

@keyframes borders {
   from {
     /* transform: scale(1, 1); */

     background-color: white;
   }

   to {
     /* transform: scale(1.1, 1.1); */
     border-radius: 5px;
   }
}


/*media query *********************************/

@media screen and (max-width:625px) {
  .skills__list {
    width: 90%;
  }
}



