/* blocks ***********************************/
.menu {
    background-color:white;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-pack: justify;
        justify-content: space-between;
    padding: 0 5%;
    height: 100%;
    -webkit-transition: all 0.7s;
    -o-transition: all 0.7s;
    transition: all 0.7s;
}
.menu__invisible-text {
    display: none;
}

.menu__link {
    color: #45217C;
    text-decoration: none;
    text-transform: uppercase;
}
.menu__heading {
    font-size: 30px;
    color: #45217C;
    font-weight: 500;
}
/* elements ***********************************/

.menu__list-item {
    display: inline-block;
    padding: 0 5px;
}

/* media queries ***********************************/

@media screen and (max-width:625px) {
.menu {
    display: inline-block;
    width: 100%;
    text-align: center;
    padding: 0;
}
ul {
    padding: 0;
}
.menu__list-item:first-child {
  border-top: 1px solid lightgray;
}
.menu__list-item {
 display: block;
 padding: 1% 0 ;
 border-bottom: 1px solid lightgray;
}
.menu__list-item:hover {
    background-color: lavender;
}
.menu__list-item.active:focus {
    background-color: #45217C;
    color:white;
}
.menu__invisible-text {
    display: inline-block;
    text-align: inherit;
    color: lightslategray;
}
.menu__heading {
   margin-bottom: 0;
} 
}


